.proceedButton {
  width: 100%;
  height: 40px;
  padding: 6px 39px 7px;
  display: inline-block;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 400;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  transition: 0.1s;
  border: none;
  text-align: center;
  background-color: #d84e55;
  color: #fff;
  margin-top: 30px;
  cursor: pointer;
}
